// @flow

import { type JsonSearchParams } from '../../types/utils'

export const search = (searchParams: JsonSearchParams, jsonObj: any): Array<any> => {
  // If jsonObj is undefined we can't compare anything and so we fail early
  if (typeof jsonObj === 'undefined') return []

  const {
    keyToMatch,
    valueToMatch,
    // keyToReturn,
    parent,
    tokenizeKeys = keyToMatch ? keyToMatch.split('.') : []
  } = searchParams

  // If we ran out of tokenizeKeys it means we need to verify that we found the right key with the correct value
  if (!tokenizeKeys.length) {
    if (typeof valueToMatch === 'undefined' || valueToMatch === jsonObj) {
      return parent ? [parent] : [jsonObj]
    }
  }

  let results = []
  const addResults = (obj: any, shiftToken?: boolean) => {
    const newSearchParams: JsonSearchParams = {
      keyToMatch,
      valueToMatch,
      // keyToReturn,
      tokenizeKeys
    }

    if (parent) newSearchParams.parent = jsonObj
    if (shiftToken) {
      newSearchParams.parent = jsonObj
      newSearchParams.tokenizeKeys = tokenizeKeys.slice(1)
      obj = [obj]
    }

    for (const key in obj) {
      const result = search(newSearchParams, obj[key])
      results = results.concat(result)
    }
  }

  // If we are searching in an Array, we would go over each child without moving the token counter
  if (Array.isArray(jsonObj)) {
    addResults(jsonObj)
  } else if (typeof jsonObj === 'object') {
    // if (firstToken === '*') {
    // } else {
    // }
    const firstToken = tokenizeKeys[0]
    // Search one level down based on the first key in tokenizeKeys with ourself as the parent
    addResults(jsonObj[firstToken], true)

    // results = results.concat(
    //   search({
    //     keyToMatch,
    //     valueToMatch,
    //     // keyToReturn,
    //     parent: jsonObj,
    //     tokenizeKeys: tokenizeKeys.slice(1)
    //   }, jsonObj[firstToken])
    // )

    // If there is no parent yet, it means that we still have the full tokenizeKeys
    // So we will try to use the same tokenizeKeys for every child of jsonObj
    if (!parent) {
      for (const key in jsonObj) {
        results = results.concat(
          search({
            keyToMatch,
            valueToMatch,
            // keyToReturn,
            tokenizeKeys
          }, jsonObj[key])
        )
      }
    }
  }

  return results
}
